import Vue from "vue";
import App from "./App.vue";
import router from "./router/router";
import store from "./store/store";
import "./registerServiceWorker"; // pwa
import "@/common/styles/base.css"; // 
import "@/common/js/rem.js"; // rem
import "swiper/dist/css/swiper.css";
import "../public/script/api.js"; // apiCloud-api
import * as method from "@/common/js/mixin.js"; // 
import API from "@/server/apis.js"; // api
import VConsole from "vconsole";
import Navigation from "vue-navigation";
import { Lazyload } from "vant";
import Bscroll from "@/components/Bscroll/Bscroll.vue";

// Bscroll
Vue.component("b-scroll", Bscroll);
// vant
Vue.use(Lazyload);

Vue.config.productionTip = false;
// 
Vue.prototype.$METHOD = method;
// API
Vue.prototype.$SERVER = API;
// 
Vue.use(Navigation, {
  router,
  moduleName: "navigation",
  keyName: "AS"
});
// console()
new VConsole();

window.apiready = function() {
  alert("4562133");
};
// apiIDapp
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
